.App-geral{
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    width: auto;
    background-color: rgb(92, 207, 169);
    height: 700px;
    padding-top: 50px;
    padding-left: 25px;
    padding-right: 5px;
    
}
.App-geral .logo{
  border-radius: 25px;
    width: 450px;
    text-align: center;
    align-items: center;
    height: 70px;
    background-color: rgb(244, 243, 245);
    }

.App-geral h1{
    font-size: 25px;
    width: auto;
   margin: 0 auto;
   color: rgb(155, 15, 15);
   
   

    }

.App-geral .conteudo{
 
width: 800px;
padding-top: 3px;


}

.App-geral ul{
    display: flex;
    margin-right: 22px;
    width: 60%;
    height: 50px;
    align-items: center;
    background-color: rgb(92, 207, 169);
    border-radius: 20px;
    text-align: center;
    justify-items: center;
    }


    .App-geral a{

        background-color: rgb(19, 155, 179);
        color: rgb(250, 249, 249);
        border: 1px solid #50f3eb;
        display: flex;
        justify-content: space-around;
        border-radius: 20px;
        padding: 5px;
        font-size: 20px;
        width: 120px;
        height: 30px;
        text-decoration: none;
        }
       
        .App-geral a:hover{
            transition-duration: 0.4s;
            background-color: #072752; 
  
        }

        .obs{
           
          width: 100%;
        }
        
        .obs p{
            color: rgb(24, 18, 18);
            width:60%;
            height: auto;
            font-size: 20px;
            background-color: rgb(247, 238, 238);
           float: right;
            padding: 20px;
            border-radius: 10px;
           margin-top: 100px;
           font-family:'Times New Roman', Times, serif;
           margin-right: 22px;
        }
        .obs p:hover{
            transition-duration: 0.4s;
            background-color: aqua; 
  
        }

        @media only screen and (max-width:480px){
            .App-geral{
              
              
              display: flex;
              min-height: 650px;
           
              
            }
            .App-geral h1{
                font-size: 20px;
                width: auto;
                margin: 0 auto;
  
            }
            .App-geral .logo{
                padding: 0px;
                
            }

            .conteudo{
             height: auto;
             
            
            }
            
       
         .obs p{
            width: auto;
            height: auto;
            margin-right: 2px;

         }
       
       
       
        }

     