
.logo{
   
    display: flex;
    justify-content: space-between;
    border: 1px solid rgb(255, 255, 253);
    align-items: center;
    height: 40px;
    text-align: center;
    font-size: 20px;
    color: rgb(255, 249, 249);
    border-radius: 5px;
    margin: 3px;
    padding-left: 5px;
    padding-right: 5px;
}

.cadastros{
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width: 100%;
    height: auto;
    background-color: rgb(92, 207, 169);
    padding-top: 10px;
    min-height:700px;
}


 .cadastro{
    display: flex;
    flex-direction: column;
    background-color: rgb(92, 207, 169);
    align-items: center;
    width: 100%;
    
}
h1 {
  
    display: flex;
    
    align-items: center;
    height: 40px;
    text-align: center;
    font-size: 20px;
    margin: 3px;
    padding-left: 5px;
    padding-right: 5px;
    color: rgb(155, 15, 15);
}

.cadastrotarefa{
    background-color: #ECE0F8;
    width: 700px;
font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    align-items: center;
    justify-content: center;
    height: auto;
    margin: 0 auto;
    padding-top: 40px;
    padding-bottom: 20px;
    padding-right: 5px;
    padding-left: 5px;
    border-radius: 10px;
    margin-top: 10px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    height: 50px;

}



input{
   
    justify-content: center;
   color: black;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    border: none;
    border-radius: 5px;
    margin: 0 auto;
   height: 30px;
    
}

.descricao{
   
    width: 80%;
    margin-right: 3px;
    color: black;
   
}




.inserir{
    width: 15%;
    height: 40px;
    border-radius: 5px;
    color: rgb(10, 10, 10);
    border-style: none;
    background-color: aqua;
    flex-direction: row;
    font-size: medium;
    align-items: center;
   justify-content: center;
    text-align: center;
   
   
}
.inserir:hover ,.remover:hover ,li:hover{
    background-color: rgb(4, 212, 212);
    color: black;
    transition-duration: 0.4s;
}
li:hover{
    background-color: rgb(37, 172, 172);
    color: black;
    transition-duration: 0.4s;
}


.remover{
    width: 10%;
    height: 25px;
    border-radius: 5px;
    color: rgb(7, 6, 6);
    border-style: none;
    background-color: aqua;
    flex-direction: row;
    font-size: medium;
    align-items: center;
   justify-content: center;
    text-align: center;
}
li{
    font-family:Georgia, 'Times New Roman', Times, serif;
    list-style: none;
    display: flex;
    justify-content: space-between;
    border: 1px solid #8f8d8d;
    align-items: center;
    height: 30px;
    text-align: center;
    font-size: 15px;
    color: rgb(12, 11, 11);
    border-radius: 5px;
    margin: 5px;
    padding-left: 5px;
    padding-right: 5px;
    justify-items: center;
    margin-bottom: 10px;
}
ul{
    width: 700px;
    background-color: #ECE0F8;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: auto;
    margin: 0 auto;
    padding: 5px;
   border-radius: 10px;
   padding-bottom: 20px;
}

.tarefas{
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    background-color: #ECE0F8;
    border-radius: 10px;
    margin-top: 0px;
    color: black;
    padding-top: 15px;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
}

.tarefas h3{
    color: rgb(155, 15, 15);
    font-size: larger; 
    padding: 15px;
    text-align: center;
}

.paginacao{
    
    margin-top: 50px;
    height: 150px;
   align-items: center;
}

.botoespgn{
    background-color: rgb(92, 207, 169);
    display: flex;
    justify-content: space-between;
   text-decoration: none;
    height: 50px;
    align-items: center;
    justify-items: center;
    margin-top: 25px;


}

.botoespgn a{
    width: 10%;
    height: 25px;
    border-radius: 5px;
    color: rgb(10, 10, 10);
    border-style: none;
    background-color: aqua;
    flex-direction: row;
    font-size: medium;
    align-items: center;
   justify-content: center;
    text-align: center;
    text-decoration: none;
    
}


@media only screen and (max-width:480px){
.cadastros{
    justify-items: center;
  
}

.cadastrotarefa {
    width: 90%;
}
.tarefas{
    width: 90%;
    padding-right: 5px;
    padding-left: 5px;
}

.inserir{
    width: 15;
    font-size: small;
    
}




.botoespgn{

   width: 95%;
   
}

.remover{
    width: auto;
}

ul{
     
    width: 95%;
}

.botoespgn a{
    display: none;
    width: 50px;
  
}

.descricao{
 
font-size: large;
}

}

@media only screen and (min-width:481px){
    .cadastros{
        justify-items: center;
      
    }
    
    .cadastrotarefa {
        width: 90%;
    }
    .tarefas{
        width: 90%;
        padding-right: 5px;
        padding-left: 5px;
    }
    
    .inserir{
        width: 15;
        font-size: small;
        
    }
    
    
    
    
    .botoespgn{
    
       width: 95%;
       
    }
    
    .remover{
        width: auto;
    }
    
    ul{
         
        width: 95%;
    }
    
    .botoespgn a{
        
        width: 50px;
      
    }
    
    .descricao{
     
    font-size: large;
    }
       

}
